import axios from 'axios'
import { MessageBox, Toast } from 'mint-ui';

// 创建axios实例
const service = axios.create({
    baseURL: '', // api 的 base_url
    // baseURL: process.env.BASE_API, // api 的 base_url
    timeout: 35000 // 请求超时时间
});

// request拦截器
service.interceptors.request.use(
    config => {
        return config
    },
    error => {
        Promise.reject(error)
    }
);

// response 拦截器
service.interceptors.response.use(
    response => {
        const res = response.data;

        return  res;
        if (res.status !== 200) {
            let msg = '';

            if (res.message !== undefined && res.message.length > 0 && typeof res.message === 'object') {
                res.message.forEach((item, index) => {
                    if (index === 0) {
                        msg += item.message
                    } else {
                        msg += ';' + item.message
                    }
                })
            } else if (res.message !== undefined && typeof res.message === 'string') {
                msg = res.message
            }

            Toast({
                message: msg,
                duration: 1000
            });

            // 50008:非法的token; 50012:其他客户端登录了;  50014:Token 过期了;
            if (res.status == 991) {
                Toast({
                    message: '获取信息失败',
                    duration: 1000
                });
                this.$router.push('/bindInfo')
            }
            if (res.status == 401 && msg != '用户名或密码错误') {

            }
            if (res.status === 403) {
                Toast({
                    message: '该用户无权限！',
                    position: 'middle',
                    duration: 5000
                });
            }
            return Promise.reject(msg)
        } else {
            return res;
        }
    },
    error => {
        return Promise.reject(error)

    }
);

export default service



