import {getPhotoList, setPhotoList,getAllPhotoList,setAllPhotoList,setUserTaker,getUserTaker} from '@/utils/auth'
import Vue from 'vue'
const photo = {
  state: {
    photoList:getPhotoList(),
    allPhotoLists:getAllPhotoList(),
    userTaker:getUserTaker()
  },
  mutations: {
    SET_PHOTOLIST: (state, obj) => {
      console.log(obj,'obj');
      if(state.photoList&&state.photoList.length>0){
        let imgList=[];
          state.photoList.forEach((item,index)=>{
            imgList.push(item.itemId)
          });
        if(imgList.indexOf(obj[0].itemId)==-1){
          state.photoList=state.photoList.concat(obj);
        }else {
          console.log(state.photoList);
          state.photoList=state.photoList.map((item,index)=>{
            if(item.itemId===obj[0].itemId){
              return{
                itemId:obj[0].itemId,
                name:obj[0].name,
                data:obj[0].data,
                photoList:obj[0].photoList,
                serverId:obj[0].serverId
              }
            }else {
              console.log(item)
              return{
                itemId:item.itemId,
                name:item.name,
                data:item.data,
                photoList:item.photoList,
                serverId:item.serverId
              }
            }
          })
        }

        }else {
          state.photoList=[...obj]
        }
      console.log(state.photoList);
      Vue.set(state, 'photoList',  state.photoList);
      setPhotoList(state.photoList);
      state.allPhotoLists.photoList=state.photoList;//设置allphotoList的photoList

      state.allPhotoLists.serviceSite.forEach((item,index)=>{//设置allphotoList的servicesite
        state.allPhotoLists.serviceSite[index].itemInfoList.forEach((option,rank)=>{
          for (let i = 0; i < state.photoList.length; i++) {
              if(state.photoList[i].itemId==state.allPhotoLists.serviceSite[index].itemInfoList[rank].itemId){
                state.allPhotoLists.serviceSite[index].itemInfoList[rank].photoList=state.photoList[i].photoList
              }
          }
        })
      });
      setAllPhotoList(state.allPhotoLists);
      console.log(state.allPhotoLists,'allphotoListstate');
    },
    CLEAR_PHOTOLIST:(state) => {
      Vue.set(state, 'photoList', []);
      setPhotoList()
    },
    SET_ALLPHOTOLIST: (state, obj) => {
      console.log(obj,'vuex-allPhotoList');
      Vue.set(state, 'allPhotoLists', obj);
      setAllPhotoList(obj)
    },
    CLEAR_ALLPHOTOLIST:(state) => {
      Vue.set(state, 'allPhotoLists', []);
      setAllPhotoList()
    },
    SET_USERTAKER: (state, obj) => {
      console.log(obj,'vuex-setUserTaker');
      Vue.set(state, 'userTaker', obj);
      setUserTaker(obj)
    },
    CLEAR_USERTAKER:(state) => {
      Vue.set(state, 'userTaker',{});
      setUserTaker()
    },
  },
  actions: {

  }
}
export default photo



