const getters = {
  code: state => state.user.code,
  currentPigfarm: state => state.user.currentPigfarm,
  token: state => state.user.token,
  signature: state => state.user.signature,
  userInfo: state => state.user.userInfo,
  photoList:state=>state.photo.photoList,
  allPhotoLists:state=>state.photo.allPhotoLists,
  serviceSites:state=>state.photo.serviceSites
}
export default getters



