import request from '@/utils/request'

export function bindUser(data) { //绑定信息
  return request({
    url: 'api/login/bind',
    method: 'post',
    data
  })
}

export function getCode(data) { //获取验证码
  return request({
    url: 'api/login/getCode',
    method: 'get',
    params:{
      telNum:data
    }
  })
}

export function login() { //登入
  return request({
    url: 'api/login/login',
    method: 'get',
  })
}
export function unbind() { //解绑
  return request({
    url: 'api/login/unbind',
    method: 'get'
  })
}

export function getUserInfoData() { //获取用户信息
  return request({
    url: 'api/myInfo/list',
    method: 'get'
  })
}

export function getInstroduce(data) { //获取介绍人信息
  return request({
    url: 'api/register/getInstroduce',
    method: 'get',
    params:{
      name:data
    }
  })
}

export function saveRegister(data) { //注册信息保存
  return request({
    url: 'api/register/save',
    method: 'post',
    data
  })
}
export function getDeptList(name,num,size) { //获取开户地
  return request({
    url: 'api/register/getDept',
    method: 'get',
    params:{
      name:name,
      pageNumber:num,
      pageSize:size
    }
  })
}




