import {setCode, getCode, setToken, getToken, setUserInfo, getUserInfo} from '@/utils/auth'
import Vue from 'vue'
const user = {
  state: {
    code: getCode(),
    currentPigfarm: {},
    token: getToken(),
    signature: {},
    userInfo:getUserInfo()
  },
  mutations: {
    SET_USERINFO: (state, obj) => {
      Vue.set(state, 'userInfo', obj)
      setUserInfo(obj)
    },
    SET_CODE: (state,code) => {
      state.code = code
      setCode(code)
    },
    SET_CURRENT_PIGFARM: (state, pigfarm) => {
      Vue.set(state,'currentPigfarm',pigfarm)
    },
    SET_TOKEN: (state, token) => {
      state.token = token
      setToken(token)
    },
    SET_SIGNATURE: (state, obj) => {
      Vue.set(state, 'signature', obj)
    }
  },
  actions: {

  }
}
export default user



