<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
  import {getQueryString} from '@/utils/wxUtil'
  import {getSignature} from '@/api/signature'
  import  {getOSType} from "./utils/wxUtil";

  export default {
    name: 'App',
    mounted() {
      this.initSignature();
    },
    methods: {
      initSignature() { // 初始化签名
        const url = location.href.split('#')[0];
        getSignature(url).then(response => {
          console.log(response);
          const signatureInfo = {
            timestamp: response.data.timestamp,
            nonceStr: response.data.nonceStr,
            signature: response.data.signature,
            appId: response.data.appid
          }
          this.$store.commit('SET_SIGNATURE', signatureInfo);
          sessionStorage.setItem("signatureInfo", JSON.stringify(signatureInfo));
        }).catch(error => {
          // this.$toast('获取签名出错：请点击右上角...里面的刷新按钮'+'url是'+url+'错误详情：'+JSON.stringify(error))
          this.$toast('网络异常，请重试！'+'url是'+url)
        })
      }
    },
    watch: {
      '$route': function (to, from){
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
      }
    }
  }
</script>
<style>
  #app {
    font-family: "PingFang SC", "Lantinghei SC", "Microsoft YaHei", arial,
    "\5b8b\4f53", sans-serif, tahoma;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #000;
  }

  body {
    margin: 0;
  }

  .iconfont {
    font-family: "iconfont" !important;
    font-size: 0.16rem;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
  }

  @font-face {
    font-family: "iconfont"; /* project id 1088322 */
    src: url("//at.alicdn.com/t/font_1088322_ajepl498q9m.eot");
    src: url("//at.alicdn.com/t/font_1088322_ajepl498q9m.eot?#iefix") format("embedded-opentype"),
    url("//at.alicdn.com/t/font_1088322_ajepl498q9m.woff2") format("woff2"),
    url("//at.alicdn.com/t/font_1088322_ajepl498q9m.woff") format("woff"),
    url("//at.alicdn.com/t/font_1088322_ajepl498q9m.ttf") format("truetype"),
    url("//at.alicdn.com/t/font_1088322_ajepl498q9m.svg#iconfont") format("svg");
  }
</style>



