import Cookies from 'js-cookie'

const TokenKey = 'Admin-Token'

export function setUserInfo(userInfo){
  sessionStorage.setItem('userInfo', JSON.stringify(
    userInfo
  ));
}

export function getUserInfo(){
  // return  JSON.parse(Cookies.get('userInfo'))
  return  JSON.parse(sessionStorage.getItem('userInfo'))
}

export function setPhotoList(photoList){
  sessionStorage.setItem('photoList', JSON.stringify(
    photoList
  ));
}

export function getPhotoList(){
  return  JSON.parse(sessionStorage.getItem('photoList'))
}

export function setAllPhotoList(photoList){
  sessionStorage.setItem('allPhotoLists', JSON.stringify(
    photoList
  ));
}

export function getAllPhotoList(){
  return  JSON.parse(sessionStorage.getItem('allPhotoLists'))
}

export function getUserTaker(photoList){
  sessionStorage.setItem('userTaker', JSON.stringify(
    photoList
  ));
}

export function setUserTaker(){
  return  JSON.parse(sessionStorage.getItem('userTaker'))
}

export function setCode(code){
  Cookies.set('code',code)

}
export function getCode(){
  return Cookies.get('code')

}

export function getToken() {
  return Cookies.get(TokenKey)
}

export function setToken(token) {
  return Cookies.set(TokenKey, token)
}

export function getLocalToken() {
  return 'token:db5a25e8c8074fc28a3483298f8524dd'

}



