import Vue from 'vue'
import Router from 'vue-router'
import {
    routes
} from './router'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import {
    getUserInfo
} from '@/utils/auth'
import {
    login,
    getUserInfoData
} from '@/api/bindInfo'
import {
    Toast
} from 'mint-ui';
import store from '@/store'

Vue.use(Router);

const router = new Router({
    routes
});
router.beforeEach((to, from, next) => {
    NProgress.start();
if (to.name == 'bindInfo' || to.name == 'Register' || to.name == 'searchAddress' || to.name == 'searchIntroducer' || to.name == 'OpenText' || to.name == 'HelpList' || to.name == 'HelpDetails') {
        next();
    } else {
        if (getUserInfo() && getUserInfo() !== "") {
            next();
        } else {
            login().then((res) => {
                if (res.status === 991) {
                    next('/bindInfo');
                } else if (res.status == 200) {
                    getUserInfoData().then((res) => {
                        let data = res.data;
                        let userInfo = {
                            id: data.id,
                            name: data.fname,
                            size: data.cfwinternum
                        };
                        store.commit("SET_USERINFO", userInfo);
                        next();
                    }).catch((err) => {
                        // Toast({
                        //     message: '获取用户信息失败，请联系管理员！',
                        //     duration: 1000
                        // });
                        next('/bindInfo');
                    });
                }
            }).catch((err) => {
                next('/bindInfo');
            });
        }
    }
   // next();
});

router.afterEach(transition => {
    NProgress.done()
});

export default router;



